<template>
  <div>
    <header class="w clearfixed">
			<h2>{{ this.topInformation }}</h2>
		</header>
  </div>
</template>

<script>
export default {
  name: 'LoginHeader',
  props :{
  	topInformation : String
  },
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	html{
				background: #fff;
			}
			* {
				font-family: PingFangSC-Regular;
				box-sizing: border-box;
			}
			
			.w {
				width: 800px;
				margin: 0 auto;
			}
			
			.clearfixed:after {
				content: ".";
				display: block;
				clear: both;
				visibility: hidden;
				height: 0;
			}
			
			header {
				height: 80px;
				line-height: 80px;
				border-bottom: 1px solid #dcdfe6;
			}
			
			header img {
				width: 1px;
				height: 28px;
				vertical-align: middle;
				float: left;
				margin: 26px 20px 0 20px;
			}
			
			h1 {
				font-size: 0;
				float: left;
				background: url(../../../assets/img/logo.jpg)no-repeat;
				width: 40px;
				height: 40px;
				margin-top: 21px;
			}
			
			h2 {
				font-size: 16px;
				float: left;
			}
			
			
</style>



