import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login/login'
import ProjectList from '@/pages/projectList/projectList'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Login',
    component: Login
  },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/projectList',
      name: 'projectList',
      component: ProjectList
    },
  ]
})



