import axios from 'axios'
import urlmap from './urlmap'

axios.defaults.withCredentials = true; // 让ajax携带cookie

/**
 * 注意：axios默认传参方式是参数放在一个json对象中
 * @param param
 * @returns {Promise<any>}
 */
const axios_post = param => {

  return new Promise((resolve, reject) => {
    console.log('axios_post-param-', param)
    let url = urlmap[param.url];
    delete param.url;
    // axios.defaults.withCredentials = true; // 让ajax携带cookie
    console.log('axios_post-axios.defaults.withCredentials-', axios.defaults.withCredentials)
    // fetch({
    //   method: 'post',
    //   url: url,
    //   data: param
    // })
    //   .then(response => {
    //     console.log(response);
    //     resolve(response)
    //   })
    
	//判断是否存在merchantId
	let totalUrl = location.href;
	let leftUrl = totalUrl.split('/#/')[0];
	let appFlag = leftUrl.indexOf('/app/');
	if(appFlag != -1){
		let merchantId = leftUrl.split('/app/')[1];
		param.merchantId = merchantId;
	}
	

    axios.post(url, param)
      .then(function (response) {
        console.log(response);
        //session失效跳转至登录界面
        if(response.data.code!= undefined && response.data.code==9){
          window.location.href="/#/login";
        }
        resolve(response.data)
      })
      .catch(function (error) {
        let result = {
          msg : "网络错误"
        }
        reject(result)
      });
  })
}

export default axios_post



