<template>
  <div>
    <login-header :topInformation='topInformation'></login-header>

    <div class="w">
      <div class="loginContent">
        <p><span>用户名</span><el-input v-model="phoneNumber" placeholder="以后可以用手机号登录" type='text' maxlength='11' minlength='11'></el-input>
        </p>
        <!--<p><span>登录密码</span><input type="password" placeholder="请填写登录密码" /></p>-->
        <p><span>登录密码</span>
          <el-input type='password' v-model="password" placeholder="请填写登录密码"></el-input>
        </p>
        <button @click="login" v-loading.fullscreen.lock="fullscreenLoading">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
  //这里是add测试
  import LoginHeader from './components/loginHeader'
  import Common from '../../components/common.js'
  import merchantService from '../../service/merchantService'
  import loginService from '../../service/loginService'


  export default {
    name: 'Login',
    components: {
      LoginHeader
    },
    data () {
      return {
        topInformation: '二永科技',
        phoneNumber: '',
        password: '',
        fullscreenLoading : false
      }
    },
    methods: {
      login() {
      	this.fullscreenLoading = true;
				let _this = this;
				_this.$router.push({path : '/projectList'})
        
      },
      
    },
    mounted() {
      
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  html {
    background: #fff;
  }

  * {
    font-family: PingFangSC-Regular;
    box-sizing: border-box;
    color: #000000;
  }

  .w {
    width: 800px;
    margin: 0 auto;
  }

  .clearfixed:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    height: 0;
  }

  .loginContent {
    font-size: 14px;
    background: #ffffff;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.06);
    border-radius: 4px;
    width: 440px;
    height: 407px;
    margin: 0 auto;
    margin-top: 40px;
    padding: 60px;
  }

  .loginContent p {
    height: 38px;
    margin-bottom: 24px;
    font-size: 0;
  }

  .loginContent p span {
    display: inline-block;
    width: 56px;
    text-align: right;
    margin-right: 14px;
    font-size: 14px;
  }

  .loginContent button {
    background: #0486fe;
    border-radius: 4px;
    width: 320px;
    height: 40px;
    color: #fff;
    margin-top: 7px;
    margin-bottom: 32px;
    cursor: pointer;
    cursor: -ms-pointer;
  }

  .loginContent p a:first-of-type {
    font-size: 14px;
    color: #606266;
    letter-spacing: 0;
    line-height: 14px;
    text-align: left;
    float: left;
  }

  .loginContent p a:last-of-type {
    font-size: 14px;
    color: #0486fe;
    letter-spacing: 0;
    text-align: right;
    line-height: 14px;
    float: right;
  }

  .loginContent p:nth-of-type(3) {
    position: relative;
  }

  .loginContent p:nth-of-type(3) img {
    width: 73px;
    height: 31px;
    position: absolute;
    right: 4px;
    top: 5px;
  }
</style>

<style>
  .loginContent p input {
    background: #ffffff;
    width: 250px;
    height: 40px;
  }

  .loginContent .el-input {
    display: inline-block;
    width: 250px;
    height: 40px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
  }
</style>



