<template>
	<div class="projectList clearfixed w">
	<h2>项目列表</h2>
	<div class="clearfixed">
		<div class="leftTab">
			<el-menu class="el-menu-vertical-demo" default-active='1' @select='handleTab'>
				<el-menu-item index="1">
					<i class="el-icon-menu"></i>
					<span slot="title">所有项目</span>
				</el-menu-item>
				<el-menu-item index="2">
					<i class="el-icon-document"></i>
					<span slot="title">未完成项目</span>
				</el-menu-item>
				<el-menu-item index="3">
					<i class="el-icon-setting"></i>
					<span slot="title">已完成项目</span>
				</el-menu-item>
			</el-menu>
		</div>
		<div class="rightContent">
			<el-button type="primary" @click='addProject'>添加项目</el-button>
				<div class="wrap">
					<el-table :data="tableData" border style="width: 900" height='700'>
						<el-table-column type='index' label="序号" width="100">
						</el-table-column>
						<el-table-column prop="projectName" label="项目名称" width="300">
						</el-table-column>
						<el-table-column prop="address" label="所在地">
						</el-table-column>
						<el-table-column prop="displayDate" label="日期" width="120">
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="100">
							<template slot-scope="scope">
								<el-button @click="handleClick(scope)" type="text" size="small">详情</el-button>
							</template>
						</el-table-column>
					</el-table>
				</div>
		</div>
	</div>
	<el-dialog :show-close=false :title="projectTop" :center=true :visible.sync="dialogFormVisible" :lock-scroll=false :close-on-click-modal=false :close-on-press-escape=false>
		<el-form :model="form">
			<el-form-item label="客户姓名：" :label-width="formLabelWidth">
				<el-input v-model="form.vipName"></el-input>
			</el-form-item>
			<el-form-item label="项目名称：" :label-width="formLabelWidth">
				<el-input v-model="form.projectName"></el-input>
			</el-form-item>
			<el-form-item label="日期：" :label-width="formLabelWidth">
				<el-date-picker
			      v-model="form.data"
			      align="right"
			      type="date"
			      placeholder="选择日期"
			      :picker-options="pickerOptions"
			      format="yyyy 年 MM 月 dd 日">
			    </el-date-picker>
			</el-form-item>
			<el-form-item label="单位：" :label-width="formLabelWidth">
				<el-input v-model="form.company"></el-input>
			</el-form-item>
			<el-form-item label="联系电话：" :label-width="formLabelWidth">
				<el-input v-model="form.phone"></el-input>
			</el-form-item>
			<el-form-item prop="email" :label-width="formLabelWidth" label="邮箱：" :rules="[
				      { required: true, message: '请输入邮箱地址', trigger: 'blur' },
				      { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
				    ]">
				<el-input v-model="form.email"></el-input>
			</el-form-item>
			<el-form-item label="详细地址：" :label-width="formLabelWidth">
				<el-input v-model="form.address"></el-input>
			</el-form-item>
			<el-form-item label="研究方向：" :label-width="formLabelWidth">
				<el-input v-model="form.research"></el-input>
			</el-form-item>
			<el-form-item label="跟踪记录：" :label-width="formLabelWidth">
				<el-input type="textarea" v-model="form.record"></el-input>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click="cancelProject">取 消</el-button>
			<el-button type="primary" @click="confirmProject">确 定</el-button>
		</div>
	</el-dialog>
	<el-dialog
		:show-close=false
	  :lock-scroll=false :close-on-click-modal=false :close-on-press-escape=false
	  title="提示"
	  :visible.sync="dialogFormVisible2"
	  width="30%">
	  <span>您确定取消吗？</span>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="cancelHandle">取 消</el-button>
	    <el-button type="primary" @click="confirmCancel">确 定</el-button>
	  </span>
	</el-dialog>
</div>
</template>

<script>
	import '../../styles/elementUIChange.css'
	export default {
		name: 'ProductList',
		data () {
			
			return {
				pickerOptions: {
		          disabledDate(time) {
		            return time.getTime() > Date.now();
		          },
		          shortcuts: [{
		            text: '今天',
		            onClick(picker) {
		              picker.$emit('pick', new Date());
		            }
		          }, {
		            text: '昨天',
		            onClick(picker) {
		              const date = new Date();
		              date.setTime(date.getTime() - 3600 * 1000 * 24);
		              picker.$emit('pick', date);
		            }
		          }, {
		            text: '一周前',
		            onClick(picker) {
		              const date = new Date();
		              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
		              picker.$emit('pick', date);
		            }
		          }]
		        },
				projectTop : '详情',
		  		dialogFormVisible : false,
		  		dialogFormVisible2 : false,
		  		formLabelWidth: '120px',
		  		form: {
				  vipName : '',
				  projectName : '',
				  data : '',
				  company : '',
				  phone : '',
				  email : '',
				  address : '',
				  research : '',
				  record : '',
				  displayDate : ''
		        },
		        form2: {
				  vipName : '',
				  projectName : '',
				  data : '',
				  company : '',
				  phone : '',
				  email : '',
				  address : '',
				  research : '',
				  record : '',
				  displayDate : ''
		        },
				tableData: [{
		          orderNumber: '1',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '2',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '3',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },{
		          orderNumber: '4',
		          projectName: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          data : '2012-02-02'
		        },],
		        addFlag : false,
		        projectIndex : ''
			}
		},
		methods: {
			getDate (date){
				let Y = date.getFullYear() + '-';
				let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
				let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
				return Y + M + D;
			},
			confirmCancel () {
				this.dialogFormVisible2 = false;
				this.addFlag = false;
			},
			cancelHandle () {
				this.dialogFormVisible = true;
				this.dialogFormVisible2 = false;
			},
			saveProject (num, projectIndex) {
				if(num == 0){
					this.tableData.push(this.form);
				}else{
					this.tableData[projectIndex] = this.form;
					
					//this.$set(this.tableData, projectIndex, this.form)
					this.form = this.form2;
				}
				localStorage.setItem('erYong', JSON.stringify(this.tableData))
			},
			addProject () {
				this.dialogFormVisible = true;
				this.addFlag = true;
			},
			handleTab (index) {
				console.log(index)
			},
			handleClick (scope) {
				this.form = this.tableData[scope.$index]
				this.dialogFormVisible = true;
				this.projectIndex = scope.$index;
			},
			confirmProject () {
				if(this.form.vipName == ''){
					this.$message.error('客户姓名不能为空');
					return;
				}
				if(this.form.projectName == ''){
					this.$message.error('项目名称不能为空');
					return;
				}
				if(this.form.data == ''){
					this.$message.error('日期不能为空');
					return;
				}
				if(this.form.address == ''){
					this.$message.error('地址不能为空');
					return;
				}
				this.form.displayDate = this.getDate(this.form.data);
				if(this.addFlag){
					this.saveProject(0)
				}else{
					this.saveProject(1, this.projectIndex)
				}
				
	    		this.dialogFormVisible = false;
	    		this.projectIndex = '';
	    	},
	    	cancelProject () {
	    		this.dialogFormVisible2 = true;
	    		this.dialogFormVisible = false;
	    	},
		},
		mounted () {
			let _this = this;
			let projects = localStorage.getItem('erYong');
			if(projects){
				_this.tableData = JSON.parse(projects);
			}else{
				_this.tableData = [];
			}
		}
	}
</script>

<style scoped lang="stylus">
.projectList
	h2
		font-size: 35px;
		text-align: center;
		margin: 20px 0 50px 0;
	.leftTab
		width: 400px;
		float: left;
		border-bottom: 1px solid #e6e6e6;
		border-top: 1px solid #e6e6e6;
	.rightContent
		width: 1000px;
		padding-left: 20px;
		float: left;
		.wrap
			height: 600px;
		button
			margin-bottom: 10px;
		.pageNumber
			text-align: center;
			margin-top: 60px;
</style>


